}
.TZ21 {
  transform: translateZ(380px);
}



/* p:hover,
h1:hover,
h2:hover,
h3:hover,
ul:hover,
a:hover {
  color: white;
} */

@media only screen and (max-width: 768px) {
  /* .header{
    display: none;
  
  } */

  .TZ1,
  .TZ2,
  .TZ3,
  .TZ4,
  .TZ5,
  .TZ6 {
    display: none;
  }

  .tilt-inner {
    width: 100%;
  }


  img {
    width: 100%;
  }
}

.displayOff {
  display: none;
  background-color: #9c4b;
}


